import './style/GymAdress.scss';
import { ReactComponent as Clock } from '../../../img/icons/clock.svg';
import { ReactComponent as Local } from '../../../img/icons/location.svg';
import { ReactComponent as Phone } from '../../../img/icons/phone.svg';
import { ReactComponent as Calendar } from '../../../img/icons/calendar.svg';
import { ReactComponent as Parking } from '../../../img/icons/parking.svg';
import Paragraph from '../../../components/UI/Paragraph/Paragraph';

export default function GymAdress({ arr }) {
  return (
    <div className='gymadress'>
      <div className='container'>
        <div className='gymadress__wrap'>
          <div className='gymadress__item gymadress__item_one'>
            <div className='gymadress__column'>
              <div className='gymadress__tittle'>
                <p>Adress:</p>
              </div>
              <div className='gymadress__block'>
                <div className='gymadress__icon'>
                  <Local />
                </div>
                <div className='gymadress__text'>
                  <p>{`${arr.name}`}</p>
                </div>
              </div>
              <div className='gymadress__block'>
                <div className='gymadress__icon'>
                  <Local />
                </div>
                <div className='gymadress__text'>
                  <p>{`${arr.adress}`}</p>
                </div>
              </div>
            </div>
          </div>
          <div className='gymadress__item gymadress__item_two'>
            <div className='gymadress__column'>
              <div className='gymadress__tittle'>
                <p>Number:</p>
              </div>
              <div className='gymadress__block'>
                <div className='gymadress__icon'>
                  <Phone />
                </div>
                <div className='gymadress__text'>
                  <p>{`${arr.number}`}</p>
                </div>
              </div>
            </div>
          </div>
          <div className='gymadress__item gymadress__item_three'>
            <div className='gymadress__column'>
              <div className='gymadress__tittle'>
                <p>Schedule:</p>
              </div>
              <div className='gymadress__block'>
                <div className='gymadress__icon'>
                  <Calendar />
                </div>
                <div className='gymadress__text'>
                  <p>{`${arr.days}`}</p>
                </div>
              </div>
              <div className='gymadress__block'>
                <div className='gymadress__icon'>
                  <Clock />
                </div>
                <div className='gymadress__text'>
                  <p>{`${arr.work}`}</p>
                </div>
              </div>
              <div className='gymadress__block gymadress__block_open'>
                <div className='gymadress__open'>
                  <Paragraph name='mini' ru='Открыто' en='Open Now' />
                </div>
              </div>
            </div>
          </div>
          <div className='gymadress__item gymadress__item_four'>
            <div className='gymadress__column'>
              <div className='gymadress__tittle'>
                <Paragraph
                  name='other'
                  ru='Преимущества спортзала'
                  en='Gym Benefits'
                />
              </div>
              <div className='gymadress__block'>
                <div className='gymadress__icon'>
                  <Parking />
                </div>
                <div className='gymadress__text'>
                  <Paragraph
                    name='other'
                    ru='Наличие парковки'
                    en='Availability of parking'
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
